.news .slider .prev,
.news .slider .next {
  position: absolute;
  width: 23px;
  height: 23px;
  border: solid #3742fa;
  border-width: 0 4px 4px 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  top: 175px;
  z-index: 1;
}
.news .slider .prev {
  left: -10px;
  -webkit-transform: translate(-50%, -50%) rotate(135deg);
  transform: translate(-50%, -50%) rotate(135deg);
}
