  border-color: #2ea2f8;
}
.popup form .close {
  position: absolute;
  right: 1.25rem;
  top: 1.25rem;
  width: 15px;
  height: 15px;
  opacity: 0.5;
  cursor: pointer;
}
.popup form .close:hover,
.popup form .close:focus {
  opacity: 1;
}
.popup form .close:before,
.popup form .close:after {
  position: absolute;
  content: ' ';
  height: 2px;
  width: 15px;
  background-color: #333;
}
.popup form .close:before {
  left: 50%;
  -webkit-transform: translate(-50%, 350%) rotate(45deg);
  transform: translate(-50%, 350%) rotate(45deg);
}
.popup form .close:after {
  right: 50%;
  -webkit-transform: translate(50%, 350%) rotate(-45deg);
  transform: translate(50%, 350%) rotate(-45deg);
}
